.container {
    width: 1200px;
    margin: 0 auto;
}

#shortcut {
    width: 100%;
    position: relative;
    z-index: 30;
    height: 40px;
    font-size: 12px;
    color: #b0b0b0;
    background: #333;

    .topnav {
        float: left;

        a {
            color: rgb(176, 176, 176);
            line-height: 40px;
            display: inline-block;

            &:hover {
                color: #fff;
            }
        }

        .sep {
            margin: 0 0.3em;
            color: #424242;
            font-family: sans-serif;
        }
    }

    .entercart {
        cursor: pointer;
        width: 120px;
        height: 40px;
        line-height: 40px;
        margin-left: 15px;
        position: relative;
        float: right;
        transition: all .2s;
        font-size: 12px;
        text-align: center;

        &:hover {
            background-color: white;
            color: #ff6700;
        }

        .icon-xiaomigouwuche {
            margin-right: 4px;
            font-size: 22px;
            line-height: 20px;
            vertical-align: -2px;
        }
    }

    .user-operation {
        float: right;

        a {
            color: rgb(176, 176, 176);
            line-height: 40px;
            display: inline-block;

            &:hover {
                color: #fff;
            }
        }

        .sep {
            margin: 0 0.3em;
            color: #424242;
            font-family: sans-serif;
        }
    }
}

header {
    width: 100%;
    height: 100px;
    position: relative;

    .header-logo {
        float: left;
        width: 62px;
        margin-top: 22px;

        a {
            position: relative;
            display: block;
            width: 56px;
            height: 56px;
            overflow: hidden;
            background-image: url(../img/logo.png);
            background-size: 100% 100%;
        }
    }

    .header-nav {
        float: left;
        width: 840px;

        .nav-list {
            position: relative;
            z-index: 10;
            float: left;
            width: 650px;
            height: 88px;
            margin: 0;
            padding-top: 12px;
            list-style-type: none;
            font-size: 16px;
            padding-left: 142px;

            .nav-items {
                cursor: pointer;
                float: left;
                font-size: 16px;
                padding: 26px 10px 38px 10px;

                &:hover {
                    color: #ff6700;
                }
            }
        }

        .nav-menu {
            width: 100%;
            position: absolute;
            top: 100px;
            left: 0;
            height: 200px;
            border-top: 1px solid #e0e0e0;
            background: #fff;
            display: none;
            box-shadow: 0 3px 4px rgb(0 0 0 / 18%);
            padding-bottom: 20px;
            z-index: 99;

            .menu-items {
                width: 1225px;
                margin: 0 auto;
                cursor: pointer;
                position: absolute;
                top: 0;
                left: 335px;
            }

            .menu-items ul li {
                float: left;
                position: relative;
                width: 180px;
                padding: 35px 12px 0;
                text-align: center;
                line-height: 20px;
                font-size: 12px;
            }

            .menu-img {
                width: 160px;
                height: 110px;
                margin: 0 auto 16px;
                text-align: center;
            }

            .menu-price {
                color: #ff6700;
            }

            .menu-title {
                width: 180px;
            }
        }
    }

    .header-search {
        float: right;
        width: 296px;
        margin-top: 25px;

        form {
            position: relative;
            width: 296px;
            height: 50px;
            z-index: 20;

            input {
                position: absolute;
                border: 1px solid #e0e0e0;
                outline: 0;
            }

            .search {
                top: 0;
                transition: all .2s;
                right: 51px;
                z-index: 1;
                width: 223px;
                height: 48px;
                padding: 0 10px;
                font-size: 14px;
                line-height: 48px;
            }

            #btn {
                cursor: pointer;
                right: 0;
                z-index: 2;
                width: 52px;
                height: 50px;
                font-size: 20px;
                line-height: 24px;
                background: #fff;
                color: #616161;
                transition: .3s;

                &:hover {
                    background: rgb(255, 103, 0);
                    color: rgb(255, 255, 255);
                }
            }
        }
    }
}

.home-swiper {
    position: relative;
    height: 460px;

    .mySwiper {
        height: 100%;

        .swiper-slide {
            img {
                width: 100%;
                height: 100%;
            }
        }

        .swiper-button-next,
        .swiper-button-prev {
            width: 41px;
            height: 69px;
            background: url(//i1.mifile.cn/f/i/2014/cn/icon/icon-slides.png) no-repeat -84px 50%;
        }

        .swiper-button-next {
            right: 0;
            background-position: -125px 50%;

            &:hover {
                background-position: -42px 50%;
            }
        }

        .swiper-button-prev {
            left: 234px;

            &:hover {
                background-position: 0 50%;
            }
        }

        .swiper-button-next:after,
        .swiper-button-prev:after {
            content: ' ';
        }

        .swiper-pagination {
            width: 400px;
            left: auto;
            right: 30px;
            bottom: 20px;
            text-align: right;
        }

        .swiper-pagination-bullet {
            width: 6px;
            height: 6px;
            border: 2px solid #fff;
            border-color: hsla(0, 0%, 100%, .3);
            border-radius: 10px;
            overflow: hidden;
            background: rgba(0, 0, 0, .4);
            opacity: 1;
        }

        .swiper-pagination-bullet-active,
        .swiper-pagination-bullet:hover {
            background: hsla(0, 0%, 100%, .4);
            border-color: rgba(0, 0, 0, .4);
        }
    }

    .site-category {
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;
        width: 234px;

        ul {
            height: 420px;
            border: 0;
            color: #fff;
            background: rgba(105, 101, 101, .6);
            padding: 20px 0;

            .category-item {
                height: 42px;
                position: relative;
                cursor: pointer;

                &:hover {
                    background-color: #ff6700;
                }

                a {
                    color: #fff;
                    display: block;
                    padding-left: 30px;
                    font-size: 14px;
                    height: 42px;
                    line-height: 42px;
                    float: left;
                }

                em {
                    position: absolute;
                    top: 12px;
                    right: 20px;
                    font-size: 16px;
                    line-height: 16px;
                    color: #e0e0e0;
                }
            }
        }
    }
}

.home-promo {
    margin-top: 15px;

    .home-promo-inner {
        .promo-nav {
            width: 234px;
            float: left;

            ul {
                margin: 0;
                padding: 3px;
                list-style-type: none;
                font-size: 12px;
                text-align: center;
                background: #5f5750;

                li {
                    position: relative;
                    float: left;
                    width: 70px;
                    height: 82px;
                    padding: 0 3px;

                    &:before {
                        position: absolute;
                        content: "";
                        background: #665e57;
                        top: -1px;
                        left: 6px;
                        width: 64px;
                        height: 1px;
                    }

                    &:after {
                        position: absolute;
                        content: "";
                        background: #665e57;
                        top: 6px;
                        left: 0;
                        width: 1px;
                        height: 70px;
                    }

                    a {
                        display: block;
                        padding-top: 18px;
                        text-overflow: ellipsis;
                        color: #fff;
                        opacity: .7;
                        font-size: 12px;
                        transition: opacity .2s;

                        &:hover {
                            opacity: 1;
                        }

                        img {
                            display: block;
                            width: 24px;
                            height: 24px;
                            margin: 0 auto 4px;
                        }
                    }
                }
            }
        }

        .promo-img {
            width: 952px;
            height: 170px;
            float: left;
            margin-left: 13px;

            ul {
                width: 954px;
                height: 170px;

                .first {
                    margin-left: 0px;
                }

                li {
                    float: left;
                    width: 308px;
                    height: 170px;
                    transition: box-shadow .2s linear, -webkit-box-shadow .2s linear;
                    margin-left: 15px;

                    img {
                        width: 100%;
                        height: 100%;
                    }
                }
            }
        }
    }
}

.home-brick-wrap {
    .home-brick-hd {
        height: 58px;
        position: relative;

        h1 {
            margin: 0;
            font-size: 22px;
            font-weight: 200;
            line-height: 58px;
            color: #333;
        }

        a {
            height: 58px;
            line-height: 58px;
            position: absolute;
            top: 0;
            right: 0;
            font-size: 16px;
            color: black;

            .iconfont {
                margin-left: 8px;
                border-radius: 16px;
                font-size: 16px;
                line-height: 12px;
                color: #b0b0b0;
            }

        }

    }

    .brick-nav {
        width: 20%;
        height: 310px;
        float: left;
        min-height: 1px;
        background-color: white;

        img {
            width: 100%;

            &:hover {
                box-shadow: 0 15px 30px rgb(0 0 0 / 10%);
                transform: translate3d(0, -2px, 0);
                transition: .5s;
            }
        }

        .first-img,
        .second-img {
            height: 310px;
        }

        .second-img {
            margin-top: 10px;
        }
    }

    .brick-list {
        width: 80%;
        float: right;
        margin: 0 0 -14px -14px;

        li {
            position: relative;
            z-index: 1;
            float: left;
            width: 226px;
            padding: 20px 0;
            height: 270px;
            margin-left: 14px;
            margin-bottom: 14px;
            background: #fff;
            text-align: center;

            &:hover {
                box-shadow: 0 15px 30px rgb(0 0 0 / 10%);
                transform: translate3d(0, -2px, 0);
                transition: .5s;
            }

            a {
                font-size: 14px;
            }

            .item-img {
                width: 160px;
                height: 160px;
                margin: 0 auto 18px;

                img {
                    width: 160px;
                    height: 160px;
                }
            }

            .item-name {
                color: #333;
                margin: 0 10px 2px;
            }

            .item-desc {
                font-size: 12px;
                color: #b0b0b0;
                margin: 0 10px 10px;
            }

            .item-price {
                color: #ff6700;
                margin: 0 10px 14px;
            }
        }

        .item-more {
            height: 93px;
            padding-top: 50px;
            text-align: left;
            padding-top: 50px;

            a {
                display: block;
                height: 48px;

                .iconfont {
                    font-size: 48px;
                    line-height: 48px;
                    color: #ff6700;
                    position: absolute;
                    right: 35px;
                    top: 48px;
                }

                .more {
                    height: 48px;
                    font-size: 18px;
                    margin: 0 110px 0 30px;
                    line-height: 1.5em;

                    p {
                        font-size: 12px;
                        color: #757575;
                    }
                }
            }
        }
    }
}

footer {
    width: 100%;
    background-color: white;
    margin-top: 20px;

    .footer-service {
        padding: 27px 0;
        border-bottom: 1px solid #e0e0e0;

        ul {
            li {
                float: left;
                width: 19.8%;
                height: 25px;
                border-left: 1px solid #e0e0e0;
                font-size: 16px;
                line-height: 25px;
                text-align: center;

                &:nth-child(1) {
                    border: none;
                }

                a {
                    color: #616161;
                }

                .iconfont {
                    margin-right: 6px;
                    font-size: 24px;
                    line-height: 24px;
                    vertical-align: -4px;
                    color: #616161;
                }
            }

            li a:hover {
                color: #ff6700;
            }

            li :hover .iconfont {
                color: #ff6700;
            }
        }
    }

    .footer-links {
        padding: 40px 0;

        ul {
            float: left;
            width: 160px;

            h4 {
                margin: -1px 0 26px;
                font-size: 14px;
                line-height: 1.25;
                color: #424242;
                font-weight: 400;
            }

            li {
                margin: 10px 0 0;
                font-size: 12px;

                a {
                    color: #757575;
                }
            }
        }

        .links-contant {
            float: left;
            width: 239px;
            border-left: 1px solid #e0e0e0;
            text-align: center;
            color: #616161;

            .phone {
                font-size: 22px;
                line-height: 1;
                color: #ff6700;
                margin-bottom: 5px;
            }

            p:nth-child(2) {
                margin: 0 0 5px 0;
                font-size: 12px;
                margin-bottom: 5px;
            }

            .btn {
                display: inline-block;
                border: 1px solid #ff6700;
                text-align: center;
                transition: all .4s;
                width: 118px;
                height: 28px;
                font-size: 12px;
                line-height: 28px;
                background: #fff;
                color: #ff6700;

                .iconfont {
                    font-size: 13px;
                }

                &:hover {
                    background-color: #ff6700;
                    color: white;
                }
            }

            .follow {
                font-size: 12px;
                margin-top: 10px;

                a {
                    width: 24px;
                    height: 24px;
                    display: inline-block;
                    vertical-align: middle;
                    margin-left: 6px;
                    margin-top: -3px;
                }

                .wb {
                    background: url(https://cdn.cnbj1.fds.api.mi-img.com/staticsfile/global/wb.png) 50% 0 no-repeat;
                    background-size: cover;
                }

                .wx {
                    background: url(https://cdn.cnbj1.fds.api.mi-img.com/staticsfile/global/wx.png) 50% 0 no-repeat;
                    background-size: cover;
                }
            }
        }
    }
}

.site-info {
    padding: 30px 0;
    font-size: 12px;

    .container {
        position: relative;

        .logo {
            width: 56px;
            height: 56px;
            margin-right: 20px;
            background: url(https://s02.mifile.cn/assets/static/image/logo-mi2.png) no-repeat;
            background-size: 56px;
            position: absolute;
            top: 0;
            left: 0;
        }

        .sites {
            margin-left: 80px;
            line-height: 18px;

            a {
                font-size: 12px;
                color: #757575;
            }

            span {
                color: #b0b0b0;
                font-size: 12px;
            }
        }

        p {
            color: #b0b0b0;
            margin-left: 80px;

            a {
                font-size: 12px;
                color: #b0b0b0;

                &:hover {
                    color: #ff6700;
                }
            }
        }
    }

}

.slogan {
    clear: both;
    margin: 30px auto 0;
    width: 100%;
    height: 19px;
    background: url(https://cdn.cnbj1.fds.api.mi-img.com/staticsfile/global/slogan2020.png) no-repeat center 0;
}

.toolbar {
    position: fixed;
    bottom: 70px;
    right: 0;
    z-index: 99;

    a {
        position: relative;
        display: block;
        width: 82px;
        height: 90px;
        margin-top: -1px;
        background-color: #fff;
        border: 1px solid #f5f5f5;
        text-align: center;
        line-height: 90px;
        text-align: center;

        .iconfont {
            font-size: 30px;
            color: #757575;
        }

        span {
            display: inline-block;
            font-size: 14px;
            width: 82px;
            height: 14px;
            line-height: 14px;
            text-align: center;
            position: absolute;
            top: 65px;
            left:0;
            color: #757575;
        }
    }
    .goTop{
        display: none;
        position: relative;
        display: block;
        width: 82px;
        height: 90px;
        margin-top: -1px;
        background-color: #fff;
        border: 1px solid #f5f5f5;
        text-align: center;
        line-height: 90px;
        text-align: center;
    }
}